<template>
	<div class="privateMessage">
		
		<el-row class="messageTopSeach">
			<el-col :span="12" class="addThePadding-LR-10"> 
				<ul class="topControlBar">
					<li><el-checkbox>全选</el-checkbox></li>
					<li>
						<el-button size="small">删除</el-button>
					</li>
					<li>
						<el-button size="small">标记为重要</el-button>
					</li>
					<li>
						<el-button size="small">举报</el-button>
					</li>
				</ul>
			<!-- <el-button></el-button> -->
			</el-col>
			<el-col :span="6" class="addThePadding-LR-10">  
				 <el-select v-model="value" placeholder="请选择">
				    <el-option
				      v-for="item in options"
				      :key="item.value"
				      :label="item.label"
				      :value="item.value">
				    </el-option>
				  </el-select>
			</el-col>
			<el-col :span="6" class="addThePadding-LR-10">
				 <el-input placeholder="请输入内容">
				    <el-button slot="append" icon="search"></el-button>
				  </el-input>
			</el-col>	
		</el-row>



		<el-row>
			<el-col :span="24">
				
				<ul class="noMessage">
					<li><i class="icon-ban"></i> 暂时没有信息</li>
				</ul>

				<ul class="hasMessage">

					<li>
						<div class="hasMessage_checkbox"><el-checkbox></el-checkbox></div>
						<div class="hasMessage_img">
							<img src="../../../assets/images/defulImage.png">
						</div>
						<div class="hasMessage_content">


							<div class="hasMessage_name read">
								<p>Your Name <span>say:</span></p>
								<p class="hasMessage_other">
									<i class="icon-clock"></i> 2017-07-27 &nbsp;
									<i class="icon-volume-2"></i> 系统消息
								</p>
							</div>
							<div class="hasMessage_messcontent read">
								Hi,How are you.
								If you want to add any feature or change existing features, you must run the tests to make sure you didn't break anything else. If you send a pull request (PR) to add something new and it doesn't have tests, or the tests don't pass, the PR won't be accepted. See contributing for more information.
							</div>
						</div>
						<div class="messRight">
								<p  class="read"></p>
								<p  class="read"><i class="icon-envelope-letter"></i> 已阅读</p>
								<el-button size="small"  @click="goToTheUrl('personalContent')">阅读私信</el-button>

						</div>
					</li>


					<li v-for="n in 6">
						<div class="hasMessage_checkbox"><el-checkbox></el-checkbox></div>
						<div class="hasMessage_img">
							<img src="../../../assets/images/defulImage.png">
						</div>
						<div class="hasMessage_content">
							<div class="hasMessage_name noread">
								<p>Your Name <span>say:</span></p>
								<p class="hasMessage_other">
									<i class="icon-clock"></i> 2017-07-27 &nbsp;
									<i class="icon-bubble"></i> 好友消息
								</p>
							</div>
							<div class="hasMessage_messcontent noread">
								Hi,How are you.
								If you want to add any feature or change existing features, you must run the tests to make sure you didn't break anything else. If you send a pull request (PR) to add something new and it doesn't have tests, or the tests don't pass, the PR won't be accepted. See contributing for more information.
							</div>
						</div>
						<div class="messRight">
								<p class="noread"><i class="icon-envelope"></i> 未阅读</p>
								<el-button size="small"  @click="goToTheUrl('personalContent')">阅读私信</el-button>
						</div>
					</li>

				</ul>

			</el-col>
		</el-row>


	</div>
</template>

<script>
  export default {
    data() {
      return {
        options: [{
          value: '占位数据',
          label: '占位数据'
        }],
        value: ''
      }
    },//data
    methods:{
			goToTheUrl(val){
				 this.$router.push('/frontEnddataList/user/'+val);
			},//goToTheUrl()
	},//methods
  }
</script>


