<template>
	<div class=" xy-subject">
		<talkMain  v-show="talkShowVal" v-on:lisnToTack="lisnToTack_on"></talkMain>
		<el-button @click="lisnToTack_on">对话框</el-button>
	</div>
</template>

<script>
	import talkMain from '@/components/tplPublic/talk/talkMain'
	export default {
	  components: {talkMain},
	  data(){
	  	return{
	  		talkShowVal:true,
	  		menuChangeShow:true,
	  	}
	  },//data()
	  methods:{
	  	lisnToTack_on(){
	  		this.talkShowVal = !this.talkShowVal;
	  	},//lisnToTack_on()
	  	callTheLeftMenu(){
	  		//alert(1)
	  		this.menuChangeShow = !this.menuChangeShow;
	  	}//callTheLeftMenu
	  },//methods
	}
</script>

<style>
	.talkMain .talkCount{
		right: 30px;
		position:absolute;
	}
</style>


