<template>
	<div class="listData">
		<section>
				<el-row class="seachCondition">

					<el-col :span="20">
						<ul class="seachCondition_list">
							<li>全部</li>
							<li>分类1</li>
							<li>分类2</li>
						</ul>
					</el-col>
					
					<el-col :span="4">
						<div class="rightControl" @click="change_seachBottomBox_type">
							<i v-if="!seachBottomBox" class="icon-magnifier"></i>
							<i v-if="seachBottomBox" class="icon-close"></i>
						</div>
					</el-col>
					
					 <transition name="el-zoom-in-top">
							<el-col v-if="seachBottomBox" :span="24" class="seachBottomBox">
