									<el-col :span="6" class="seachBottomBox_fragment">
										<el-input placeholder="请输入内容">
										    <el-button slot="append" icon="search"></el-button>
										</el-input>
									</el-col>

								</el-row>
							</el-col>
					</transition>

				</el-row>
		</section>

		<section class="listData_contentStyle">
				<el-row class="listData_contentRowBox">

					<el-col :span="6" class="article_contentcard_box">
