</template>

<script>
	export default {
		data(){
			return{
				seachBottomBox:false,
			}
		},//data
		methods:{
			change_seachBottomBox_type(){
				this.seachBottomBox = !this.seachBottomBox;
			},//change_seachBottomBox_type
			
		},//methods
	}
</script>



