<template>
	<div class="floatMian">
		<el-row class="theTable">
			<el-col :span="24" class="tableTitle">
				<div class="tableTitleText">
					<i class="icon-equalizer"></i>	控制面板
				</div>
			</el-col>
           	<el-col :span="24" class="tableContent">

				<el-button @click="changTheFloat_call()"><i class="icon-arrow-left-circle"></i> 取消返回</el-button>
				<el-button v-if="floatType =='add'" type="primary" @click="changTheFloat_call('add')"><i class="icon-plus"></i> 确认新增</el-button>
				<el-button v-if="floatType =='look'" type="primary" @click="changTheFloat_call('modify')"><i class="icon-plus"></i> 保存修改</el-button>


			</el-col>
	    </el-row>

	    <el-row class="theTable">
			<el-col :span="24" class="tableTitle">
				<div class="tableTitleText">
					<i class="icon-equalizer"></i>	控制面板
				</div>
			</el-col>
           	<el-col :span="24" class="tableContent">
           			浮动框类型：{{floatType}}<br><br>
			</el-col>
	    </el-row>
	</div>
</template>

<script>
	export default {
  		name:'floatMian',
  		props:['floatType'],
    	data(){
    		return{

    		}
    	},//data()
    	methods:{
    		changTheFloat_call(msg){
    			this.$emit('lisnTheFn',msg);
    		},//changTheFloat_call
    	},//methods
	}
</script>


