<template>
	<div class="defaultPage creatComponents">

		<ul>
			<li v-for="n in componentsVal">

				<el-row>

          <child class="el-col layout_domElement_box " :class="a.domElement_colclass" v-for="a in n.child" :tabsName="a.childName ? a.childName:'coustomDefault'" :defaultChild="a" :rowNum="n" @f_thisClick="thisClick_f"><solt></solt></child>
          <!--<coustomDefault v-for="a in n.child" v-if="!a.childName" :defaultChild="a" :rowNum="n" @f_default="default_f"></coustomDefault>-->
          <!--<child v-for="a in n.child" v-if="a.childName"   :tabsName="a.childName" ><solt></solt></child>-->
          <!-- {{componentsVal}} -->
					<!--<div  class="el-col layout_domElement_box" v-for="a in n.child" :class="a.domElement_colclass" >-->
						<!--<div v-if="!a.childName" class="layout_domEvent" @click="textFn(n,a)">-->
							<!--<i class="icon-plus"></i>-->
		 					<!--添加新组建-->
						<!--</div>-->


            <!--<child   :tabsName="a.childName" ><solt></solt></child>-->
            <!--{{componentsVal}}-->
					<!--</div>-->
          <!--<div class="el-col layout_domElement_box">-->
            <!--<child v-for="item in moduleData[point.split('-')[0]-1]" :tabsName="item" ><solt></solt></child>-->
          <!--</div>-->


				</el-row>

			</li>
		</ul>

		 <div class="creatTheButton"  @click="dialogVisible = true">
		 	<i class="icon-plus"></i>
		 	添加布局
		 </div>



		 <!--配置框-->
		 <el-dialog
		  title="选择布局样式"
		  class="layout_dialog"
		  :visible.sync="dialogVisible"
		  :modal-append-to-body=false
		  size="large">
		  <div>
		  	<el-row>

		  		<el-col :span="24" class="layout_imgLine">

		  			<p class="layout_textTitle">布局样式</p>

		  			<el-radio-group v-model="from.radio">
		  				<el-radio :label="1">
		  					<div class="layoutImg_box rowone">
		  						整行布局
		  					</div>
		  				</el-radio>
					    <el-radio :label="2">
					    	<div class="layoutImg_box rowtwo">
					    		对称布局
					    	</div>
					    </el-radio>
					    <el-radio :label="3">
					    	<div class="layoutImg_box rowthree">
					    		三格布局
					    	</div>
					    </el-radio>
					    <el-radio :label="4">
					    	<div class="layoutImg_box rowfour">
					    		四格布局
					    	</div>
					    </el-radio>
					   <!--   <el-radio :label="5">
					    	<div class="layoutImg_box rowfire">
					    		左窄右宽
					    	</div>
					    </el-radio>
					    <el-radio :label="6">
					    	<div class="layoutImg_box rowsix">
					    		左宽右窄
					    	</div>
					    </el-radio> -->
              <el-radio :label="8">
                <div class="layoutImg_box roweight">
                  左右调整
                </div>
              </el-radio>

					    <el-radio :label="7">
					    	<div class="layoutImg_box rowseven">
					    		自定义宽
					    	</div>
					    </el-radio>
					</el-radio-group>

		  		</el-col>

		  		<el-col :span="24" class="layout_attribute" v-show="from.hasLayoutAttribute">

		  			<p class="layout_textTitle">布局属性</p>
		  			<p class="layout_textTitleNotes">提示：布局数分为24格，左边栅格数和右边栅格数总计之和必须为24。</p>

		  			<ul class="el-row layout_attributeUL">
		  				<li class="el-col el-col-8">
		  					<span>左边栅格数</span>
		  					<el-input-number v-model="from.num_leftWidth" :min="from.num_min" :max="from.num_max" @change="left_numWidthChange"></el-input-number>
		  				</li>
		  				<li class="el-col el-col-8">
		  					 <span>右边栅格数</span>
		  					<el-input-number v-model="from.num_rightWidth" :min="from.num_min" :max="from.num_max" @change="right_numWidthChange"></el-input-number>
		  				</li>
		  			</ul>

		  		</el-col>

		  	</el-row>
		  </div>
		  <span slot="footer" class="dialog-footer">
		    <el-button @click="dialogVisible = false">取 消</el-button>
		    <el-button type="primary" @click="creatC">确 定</el-button>
		  </span>
		</el-dialog>
		<!--配置框-->
    <!--组件选择-->
    <el-dialog
      title="提示"
      :visible.sync="chooseModuleDig"
      size="small"
      :modal-append-to-body=false
     >
     <div>
       <el-row>
         <el-col :span="24" class="layout_imgLine">
           <p class="layout_textTitle">组件</p>
           <el-radio-group v-model="tabsName">
             <el-radio label="coustomTable">
               <div class="layoutImg_box rowone">
                 表格
               </div>
             </el-radio>
             <el-radio label="coustomForm">
               <div class="layoutImg_box rowtwo">
                 表单
               </div>
             </el-radio>
             <el-radio label="coustomCarousel">
               <div class="layoutImg_box rowthree">
                 走马灯
               </div>
             </el-radio>
           </el-radio-group>

         </el-col>
				 <el-col :span="24" class="layout_imgLine">
						<el-form v-model="tableParam">

						</el-form>
				 </el-col>
       </el-row>
     </div>
      <span slot="footer" class="dialog-footer">
    <el-button @click="chooseModuleDig = false">取 消</el-button>
    <el-button type="primary" @click="f_createModule">确 定</el-button>
  </span>
    </el-dialog>
    <!--组件选择-->

	</div>
</template>

<script>
  import { mapGetters } from 'vuex'
    import coustomDefault from '@/components/creatPage/public/module/default'
    import coustomTable from '@/components/creatPage/public/module/table'
    import coustomForm from '@/components/creatPage/public/module/form'
    import coustomCarousel from '@/components/creatPage/public/module/Carousel'
  export default {
  	 components:{child:{
  	     name:'child',
       components:{coustomTable,coustomForm,coustomCarousel,coustomDefault},
       props:{
         tabsName:{
           type:String,
           required: true
         },
         defaultChild:{
             type:Object,
           required:true,
         },
         rowNum:{
             type:Object,
            required:true
         }
       },
       render(h){

         return (
          <div >
           <this.tabsName ref='ref'  nativeOnClick={this.thisClick} >
           </this.tabsName>
           </div>
       )

       },
       created(){
           console.log(this.defaultChild)
           this.$store.commit('f_defaultChild',this.defaultChild);
           this.$store.commit('f_rowNum',this.rowNum)
       },
       methods:{
				 update_f(){
					 console.log(111)
				 },
         thisClick(){
             let temp = [];
             temp[0] = this.rowNum;
             temp[1] = this.defaultChild;
             this.$emit('f_thisClick',temp)
             console.log(this.defaultChild)
         }
       }
     }},
    data() {
      return {

				
        chooseModuleDig:false,
        moduleData:[],
        tabsName:'coustomDefault',
        dialogVisible: false,
        point:'',
        from:{
        	radio:1,
        	input_height:200,
        	num_leftWidth:6,
        	num_rightWidth:18,
        	num_min:1,
        	num_max:23,
        	hasLayoutAttribute:false
        },
        componentsValID:0,
        componentsVal:[],
        thisClickPoint:'',
				paramsShow:''
      };
    },//data
    methods: {
			
      f_createModule(){
          console.log(this.componentsVal)
          console.log(this.thisClickPoint)
          console.log(this.tabsName)
        this.componentsVal[this.thisClickPoint.split('-')[0] - 1].child[this.thisClickPoint.split('-')[1] - 1].childName = this.tabsName


          console.log(this.componentsVal)
        this.chooseModuleDig = false
      },
      thisClick_f(msg){
          console.log(msg)
				
        this.thisClickPoint = msg[0].id+'-'+msg[1].domID
          if(!this.componentsVal[msg[0].id - 1].child[msg[1].domID - 1].childName || this.componentsVal[msg[0].id - 1].child[msg[1].domID - 1].childName == 'coustomDefault'){
              this.chooseModuleDig = true
          }
       
      },
      default_f(msg){
          console.log(msg)
        this.textFn(msg[0],msg[1])
      },
      creatC(){
      	this.componentsValID++;
      	switch(this.from.radio){
      		case 1:
      			this.componentsVal.push(
		      		this.creatElementDom(this.componentsValID,"el-col-24",this.from.radio)
		      	);
      		break;
      		case 2:
      			this.componentsVal.push(
		      		this.creatElementDom(this.componentsValID,"el-col-12",this.from.radio)
		      	);
		    break;
		    case 3:
		    	this.componentsVal.push(
		      		this.creatElementDom(this.componentsValID,"el-col-8",this.from.radio)
		      	);
		    break;
		    case 4:
		    	this.componentsVal.push(
		      		this.creatElementDom(this.componentsValID,"el-col-6",this.from.radio)
		      	);
		    break;
		    // case 5:
		    // 		this.componentsVal.push({
		    //   				id:this.componentsValID,
		    //   				domElement_count:0,
				  //     		child:[
				  //     			{domElement_colclass:'el-col-'+this.from.num_leftWidth,domID:1},
				  //     			{domElement_colclass:'el-col-'+this.from.num_rightWidth,domID:2},
				  //     		]
		    //   		});
		    // break;
		    // case 6:
		    // 		this.componentsVal.push({
		    //   				id:this.componentsValID,
		    //   				domElement_count:0,
				  //     		child:[
				  //     			{domElement_colclass:'el-col-'+this.from.num_leftWidth,domID:1},
				  //     			{domElement_colclass:'el-col-'+this.from.num_rightWidth,domID:2},
				  //     		]
		    //   		});
		    // break;
         case 8:
           this.componentsVal.push({
                 id:this.componentsValID,
                 domElement_count:0,
                 child:[
                   {domElement_colclass:'el-col-'+this.from.num_leftWidth,domID:1},
                   {domElement_colclass:'el-col-'+this.from.num_rightWidth,domID:2},
                 ]
             });
        break;
		    case 7:
		    	 this.$message({
		          message: '抱歉，目前还不支持自定义设置，请选择其他设置！',
		          type: 'error'
		        });
    			return false;
    		break;
      	}
        this.$store.commit('f_componentsVal',this.componentsVal);
		this.dialogVisible = false
      },//creatC()
      creatElementDom(idVal,className,countVal){
      			let arr=[];
      			for(let i=0;i<countVal;i++){
		      			arr.push({domElement_colclass:className,domID:i+1});
		      		}
      			return{
      				id:idVal,
		      		child:arr,
      			}
      },//creatElementDom
      left_numWidthChange(val){
      		this.from.num_rightWidth=24-val;
      },//left_numWidthChange
      right_numWidthChange(val){
      		this.from.num_leftWidth=24-val;
      },//right_numWidthChange
      textFn(val,node){
    	console.log(val)
    	console.log(node)
        this.point = val.id +'-'+node.domID;

//        console.log(JSON.stringify(this.moduleData))
//        this.moduleData[val.id-1][node.domID-1] = 'coustomTable'
//      console.log(JSON.stringify(this.moduleData))
//        this.moduleData[Number(val.id)-1][Number(node.domID)-1];
//        console.log(this.moduleData)
      }
    },//methods

    computed:{
      ...mapGetters([
        'f_getComponentsVal',
      ]),
      ClickPointThis(){
          return this.tabsName
      },
      pointData(){
          return this.point
      },
    	aaa(){
    		return this.from.radio;
    	},

    },
    watch:{
			tabsName(val){
				console.log(val)
				this.paramsShow = val;
			},
      f_getComponentsVal(val){
          console.log(val)
          this.componentsVal = val
      },
      pointData(value){
          console.log(value)
//        this.componentsVal[value.split('-')[0] -1].child[value.split('-')[1] -1].childName = 'coustomTable'
      },
//      module(val){
//          console.log(val)
//      },
    	aaa(val){
    		//console.log(val)
    			switch(val){
    			case 1:
    				this.from.hasLayoutAttribute = false;
    			break;
    			case 2:
    				this.from.hasLayoutAttribute = false;
    			break;
    			case 3:
    				this.from.hasLayoutAttribute = false;
    			break;
    			case 4:
    				this.from.hasLayoutAttribute = false;
    			break;
    			// case 5:
    			// this.from.hasLayoutAttribute = true;
    			// 	this.from.num_leftWidth = 6;
    			// 	this.from.num_rightWidth = 18;
    			// break;
    			// case 6:
    			// 	this.from.hasLayoutAttribute = true;
    			// 	this.from.num_leftWidth = 18;
    			// 	this.from.num_rightWidth = 6;
    			// break;
            case 8:
          this.from.hasLayoutAttribute = true;
            this.from.num_leftWidth = 6;
            this.from.num_rightWidth = 18;
          break;
    			case 7:
    				this.$message({
				          message: '抱歉，目前还不支持自定义设置，请选择其他设置！',
				          type: 'error'
				        });
    				//this.from.hasLayoutAttribute = true;
    			break;
    		}

    	},//aaa()


    },//watch

  };
</script>



