<template>
	
	<div class="topMenuBar">
				<el-row class="topMenuBar_next">
					<el-col :span="6" class="topMenuLogo">
						<img src="../../../assets/images/logo1.png">
					</el-col>
					<el-col :span="18" class="topMenu">
						<ul class="fristMenu">

							<li  v-for="item in menuVal" >
								<div class="farItemDiv" v-if="item.childrenMenu">
									{{item.name}}
									<ul   class="childrenMenu">
										<li v-for="itemChild in item.childrenMenu" @click="gotoPage(itemChild.path)">
											{{itemChild.name}}
										</li>
									</ul>
								</div>
								<div v-else @click="gotoPage(item.path)">
									{{item.name}}
								</div>
							</li>
						
						</ul>
					</el-col>
				</el-row>
	</div>

</template>

<script>
	export default {
		data(){
			return{
				menuVal:[
					{path:'/frontEndIndexPage',name:'首页'},
					{
						name:'列表及内容',childrenMenu:[
							{path:'/frontEnddataList',name:'列表页样式'},
							{path:'/frontEnddataList/articlePage',name:'文章内容样式'},
							{path:'/frontEnddataList/articlePagetwo',name:'文章内容样式2'},
							{path:'/frontEnddataList/timeline',name:'时间轴'},
							{path:'/frontEnddataList/authorizationPage',name:'授权页面'},

						]
					},
					{path:'/frontEnddataList/user',name:'用户管理'},
					{path:'/',name:'管理后台'},
				],
			};
		},//data()
		methods:{
			gotoPage(val){
				// alert(1)
				 this.$router.push({path:val});
			},//gotoPage()
		},
	}
</script>


