<template>
	
	<div class="authorizationPage userPage_mypage">
		
		<div class="topBanner300">
			<h1>Your Name</h1>
			<p class="topBanner300_p">- 内容可以自定义，但是不要太长 - </p>
		</div>

		<div class="authorOfArticle">

				<div class="headImg_Modular">
					<div class="headImg_boxRight">
						<img src="../../../assets/images/defulImage.png">
					</div>
					<div class="headImg_botton_menu">
							
							<p class="headImg_left_menutitle">
								普通会员菜单功能
							</p>

							<ul>

								<li @click="goToTheUrl('personal')">
									<i class="icon-user"></i> 个人资料
								</li>
								<li @click="goToTheUrl('modifyPassWord')">
									<i class="icon-note"></i> 修改密码
								</li>
								<li @click="goToTheUrl('myFriends')">
									<i class="icon-people"></i> 我的好友
								</li>
								<li  @click="goToTheUrl('myArticle')">
									<i class="icon-envelope"></i> 我的文章
								</li>
								<li @click="goToTheUrl('privateMessage')">
									<i class="icon-envelope"></i> 我的私信
								</li>
								
								<li @click="goToTheUrl('myFavorites')">
									<i class="icon-envelope"></i> 收藏夹
								</li>
								<li @click="goToTheUrl('personal')">
									<i class="icon-note"></i> 新建文章
								</li>
							</ul>

							<p class="headImg_left_menutitle">
								内部会员菜单功能
							</p>

							<ul>
								<li @click="goToTheUrl('myProject')">
									<i class="icon-user"></i> 我的项目
								</li>
								<li @click="goToTheUrl('apiPageApiListPage')">
									<i class="icon-user"></i> api接口
								</li>
							</ul>
					</div>
				</div>
				
				<div class="authorOfArticle_list">
					
				</div>
		</div>

		<div class="stationery">
			<div class="stationery_content">

				<el-row class="stationery_content_layout">
					<el-col :span="24" class="personal_title">
						
						{{$route.name}}

					</el-col>

					<el-col :span="24">

							<transition name="el-zoom-in-center">
							    <router-view></router-view>
							</transition>

					</el-col>
				</el-row>

			</div>
		</div>

	</div>

</template>


<script>
	export default {
		methods:{
			goToTheUrl(val){
				 this.$router.push('/frontEnddataList/user/'+val);
			},//goToTheUrl()
		}
	}
</script>


