<template>

	<div class="mapShow">
		
		<!--地图-->
		<putMap></putMap>
		<!--地图-->

		<!--左侧浮动-->
		<transition name="el-zoom-in-center">
			<leftController v-if="leftControllerType"></leftController>
		</transition>
		<!--左侧浮动-->
		
		<!--控制搜素条件的控件-->
		<div class="typeController" @click="changeTypeState">
			<i v-if="leftControllerType" class="icon-size-actual"></i>
			<i v-else class="icon-size-fullscreen"></i>
		</div>
		<!--控制搜素条件的控件-->

		<!--右侧浮动-->
		<rightController></rightController>
		<!--右侧浮动-->

	</div>

</template>

<script>
import putMap from '@/components/newMap/putThemap'
import leftController from '@/components/newMap/leftController'	
import rightController from '@/components/newMap/rightController'	


export default {
	components:{putMap,leftController,rightController},
	data(){
		return{
			leftControllerType:false,
		}
	},//data()
	methods:{
		changeTypeState(){
				this.leftControllerType = !this.leftControllerType;
			},//changeTypeState
	},//methods
}
</script>


