<template>

    <div>

        <ul class=" write_parameter">
            <li v-for="(n,index) in itemULarr" >
                <el-row>
                    <el-col :span="2" class="checkbox">
                        <el-checkbox v-model="n.checkbox"></el-checkbox>
                    </el-col>
                    <el-col :span="10" class="input" >
                        <el-input v-model="n.key" placeholder="请输入内容"></el-input>
                    </el-col>
                    <el-col :span="10" class="input">
                        <el-input  v-model="n.value" placeholder="请输入内容"></el-input>
                    </el-col>
                    <el-col :span="2" class="checkbox">
                        <el-button  @click="itemULarr.splice(index, 1)"><i class="icon-close"></i></el-button>
                    </el-col>
                </el-row>
            </li>

            <li class="write_parameter_bottom">
                <el-button @click="addInput" type="primary">添加一个</el-button>
                <!-- <el-button @click="removeInput" :disabled="inputDisabled">去掉一个</el-button> -->
            </li>
	    </ul>

    </div>
  
</template>

<script>
export default {
    data() {
        return {
            itemUL:1,
            itemULarr:[
                {id:1,key:'',value:'',checkbox:true},
            ],
        }
    },//data
    methods: {
        addInput(){
      	this.itemUL+=1;
      	//this.itemUL.len+=1;
      	this.itemULarr.push({id:this.itemUL,key:'',value:'',checkbox:true});
      },//addInput
       removeRowInput(val){
      	this.itemULarr.splice(val, 1)
      },//removeRowInput
    },//methods
}
</script>



