<template>

		<div class="">

			<div class="newMap_rightController">
				<ul>
					<li @click="changeTypeState"><i class="icon-screen-desktop"></i></li>
				</ul>
			</div>

			<transition name="el-zoom-in-center">
					<div v-if="typeState" class="floatRight_Content">
						<el-row class="floatRight_typeState">
							<el-col :span="24">
								<el-button @click="changeTypeState"><i class="icon-share-alt"></i> 关闭</el-button>
							</el-col>
						</el-row>

						<el-row >
							<el-col :span="24">

								<el-collapse v-model="activeNames" >
									  <el-collapse-item title="警务信息" name="1">
									    <el-row>
									    	<el-col :span="24" class="bottomLine">
									    		  <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">全选</el-checkbox>
												  <div style="margin: 15px 0;"></div>
												  <el-checkbox-group v-model="checkedCities" @change="handleCheckedCitiesChange">
												    <el-checkbox v-for="city in cities" :label="city" :key="city">
												    	<img src="../../assets/images/cs_dp.svg" class="checkboxImg">
												    	{{city}}
												    </el-checkbox>
												  </el-checkbox-group>
									    	</el-col>
									    </el-row>
									  </el-collapse-item>
								</el-collapse>
							
							</el-col>
						</el-row>

					</div>
			</transition>
			
		</div>
	
		
</template>

<script>
	const cityOptions = ['上海', '北京', '广州', '深圳'];
	export default {
		data(){
			return{
				typeState:false,
				activeNames: ['1'],
				checkAll: true,
		        checkedCities: ['上海', '北京'],
		        cities: cityOptions,
		        isIndeterminate: true
			}
		},//data()
		methods:{
			changeTypeState(){
				this.typeState = !this.typeState;
			},//changeTypeState
			handleCheckAllChange(event) {
		        this.checkedCities = event.target.checked ? cityOptions : [];
		        this.isIndeterminate = false;
		    },
		    handleCheckedCitiesChange(value) {
		        let checkedCount = value.length;
		        this.checkAll = checkedCount === this.cities.length;
		        this.isIndeterminate = checkedCount > 0 && checkedCount < this.cities.length;
		    },
		    
		},//methods
	}
</script>


