<template>
	<div class="titlePageTop">
		<el-row>
			<el-col :span="20">
				 <ul>
				 	<li>当前页面：<span class="bg-blue labelSpan inline spanIcon"><i class="icon-folder-alt"></i>  {{title}}</span> </li>
				 </ul>
			</el-col>
		</el-row>
	</div>
</template>

<script>

  export default {

    data() {
    return{
      title:this.$route.name
    }
    },//data()
    watch:{
      $route(to,from){
          this.title = to.name;
       // this.isLogin('/sessions/getcurrentuser');
          console.log(to);
          console.log(from);
      }
    },
   
    
  }
</script>


