<template>
	<div class="apidebugging">
		<el-row class="rowAddMargin-bottom">
			<el-col :span="18" class="apidebugging_topline">

				 <el-input placeholder="请输入内容" v-model="input5">
				    <el-select v-model="select" slot="prepend" placeholder="请选择">
				      <el-option v-for="item in selectData" :label="item.name" :value="item.name"></el-option>
				    </el-select>
				    <el-button slot="append" icon="search">Params</el-button>
				 </el-input>

			</el-col>

			
			<el-col :span="6" class="apidebugging_topline">

				<el-dropdown split-button>
				  send
