				    <el-dropdown-item>黄金糕</el-dropdown-item>
				  </el-dropdown-menu>
				</el-dropdown>

			</el-col>

		</el-row>


		<el-row class="rowAddMargin-bottom">

			<el-col :span="24" class="apidebugging_topline">

				<el-tabs v-model="activeName" @tab-click="handleClick">

				    <el-tab-pane label="Authorization" name="Authorization">
				    	<!--Authorization-->
				    		<el-row>
				    			
								<el-col :span="18">
				    				
				    			</el-col>
				    			
								<el-col :span="6">
				    				
				    			</el-col>

				    		</el-row>
				    	<!--Authorization-->
				    </el-tab-pane>

				    <el-tab-pane label="Headers" name="Headers">
				    	<!--Headers-->
				    		<el-row>
				    			<el-col :span="18">
				    				<fromData></fromData>
				    			</el-col>
				    			<el-col :span="6">
				    				
				    			</el-col>
				    		</el-row>
				    	<!--Headers-->
				    </el-tab-pane>

				    <el-tab-pane label="Body" name="Body">
						<!--Body-->
							<el-row>
								
								 <el-col :span="24" class="bodyTop_typeLine">
									<ul class="bodyTop_typeLine_ul">
										<li><el-radio class="radio" v-model="bodyRadio" label="1">form-data</el-radio></li>
										<li><el-radio class="radio" v-model="bodyRadio" label="2">x-www-form-urlencoded</el-radio></li>
										<li><el-radio class="radio" v-model="bodyRadio" label="3">raw</el-radio></li>
										<li><el-radio class="radio" v-model="bodyRadio" label="4">binary</el-radio></li>
									</ul>
								</el-col> 

								<el-col :span="18" class="">
									
									<fromData></fromData>
									
								</el-col>

							</el-row>
						<!--Body-->
					</el-tab-pane>

				    <el-tab-pane label="Pre-request Script" name="Script">Pre-request Script</el-tab-pane>
				</el-tabs>

			</el-col>
			
		</el-row>

		<!--请求失败了...-->
		<el-row>
			<el-col :span="24" class="apidebugging_topline " >
				<p>请求结果：</p>
			</el-col>
			<el-col :span="24" class="apidebugging_topline bottom_sendLine">
			
