					Why this might have happened:<br>
					The server couldn't send a response:<br>
					Ensure that the backend is working properly<br>
					SSL connections are being blocked:<br>
					Fix this by importing SSL certificates in Chrome<br>
					Cookies not being sent:<br>
					Use the Postman Interceptor extension<br>
					Request timeout:<br>
					Change request timeout in Settings > General<br></p>
				</div>

			</el-col>
		</el-row>
		<!--请求失败了...-->

	</div>
</template>



<script>
import fromData from '@/components/frontEnd/apiPage/fromData';

export default {
	components: {
      fromData
    },
  data() {
    return {
	  bodyRadio:'1',
      inputDisabled:true,
      activeName: 'Headers',	
      input3: '',
      input4: '',
      input5: '',
      select: '',
      selectData:[
      	{name:'POST'},
      	{name:'GET'},
      	{name:'PUT'},
      	{name:'PATCH'},
      	{name:'DELETE'},
      	{name:'COPY'},
      	{name:'HEAD'},
      	{name:'OPTIONS'},
      	{name:'LINK'},
      	{name:'UNLINK'},
      	{name:'PURGE'},
      	{name:'LOCK'},
      	{name:'UNLOCK'},
      	{name:'PROPFIND'},
      	{name:'VIEW'},
      ],
    }
  },
  methods: {
      handleClick(tab, event) {
        console.log(tab, event);
      },//handleClick
  
    },//methods
    watch:{
    	
    },
}
</script>


