<template>
		<div id="WebGL-output">
		</div>
</template>

<script>
//let renderer,planeGeometry,scene,camera;
	export default {
		data(){
			return{
				renderer:{},
				planeGeometry:{},
				scene:{},
				camera:{}
			}
		},//data()
		methods:{
			//初步动画示例---------------->start
			initTHREE(){
			     	   this.scene = new THREE.Scene();

			           this.camera = new THREE.PerspectiveCamera(
			                    50,
			                    window.innerWidth / window.innerHeight,
			                    0.1,1000
			            );

			            this.camera.position.x = -20;
			            this.camera.position.y = 60;
			            this.camera.position.z = 50;
			            this.camera.lookAt(this.scene.position);
			            this.scene.fog = new THREE.Fog(0xffffff,0.005,100);
			            this.renderer = new THREE.WebGLRenderer();
			            this.renderer.setClearColor( new THREE.Color(0xefefef,0.5) );
			            this.renderer.setSize(window.innerWidth,window.innerHeight);
			            this.renderer.shadowMapEnabled = true;

			            this.planeGeometry = new THREE.PlaneGeometry(70,50,1,1);
			            var planeMaterial = new THREE.MeshLambertMaterial({color:0xbbbccc});
			            var plane = new THREE.Mesh(this.planeGeometry,planeMaterial);

			            plane.rotation.x = -.5 * Math.PI;
			            plane.position.x = 0;
			            plane.position.y = 0;
			            plane.position.z = 0;

			            this.scene.add(plane);

			            var ambientLight = new THREE.AmbientLight(0x0c0c0c);
			            this.scene.add(ambientLight);

			            var spotLight = new THREE.SpotLight(0xffffff);
			            spotLight.position.x = -40;
			            spotLight.position.y = 60;
			            spotLight.position.z = -10;

			            this.scene.add(spotLight);

			            //document.body.appendChild(renderer.domElement);
			            document.getElementById("WebGL-output").append(this.renderer.domElement);
			            this.render();

			},//init_THREE
			addCube(){
					    var cubeGeometry = new THREE.BoxGeometry(5,5,5);
					    var cubeMaterial = new THREE.MeshLambertMaterial({color:0xCCCFFF});
					    var cube = new THREE.Mesh(cubeGeometry,cubeMaterial);
					    cube.position.x = -30 + Math.round(Math.random() * this.planeGeometry.parameters.width);
					    cube.position.y = Math.round(Math.random()*5);
					    cube.position.z = -20 + Math.round(Math.random() * this.planeGeometry.parameters.height);
					    this.scene.add(cube);
			},//addCube()
			render(){
				       this.addCube();
				       this.renderer.render(this.scene,this.camera);
				       requestAnimationFrame(this.render);
			},//render()
			//初步动画示例---------------->end
			
				
		},//methods
		mounted(){
			this.initTHREE();
		},//mounted
	}
</script>
<style>
	.threeText{
		width: 100%;
		height: 100%;
		overflow: hidden;
		background-color: #ccc;
	}
</style>
	


