<template>
	<div class="veiwMain">
		<!-- <transition name="el-zoom-in-top">
				<talkMain v-show="talkShowVal" v-on:lisnToTack="lisnToTack_on"></talkMain>
			</transition> -->
		<div class="tacTop">

			<!--聊天对话框组件-->
			
			<!--聊天对话框组件-->
			<headerTop  v-on:menuChangEventGoOn="callTheLeftMenu"></headerTop>
			<leftMenu  v-bind:menuChangeShow="menuChangeShow"></leftMenu>
			<div class="fulhlWidt overf_auto">
				<titlePageTop></titlePageTop>
				<transition 
				 name="custom-classes-transition"
    			 enter-active-class="animated bounceInLeft">
					<router-view></router-view>
				</transition>
			</div>
		</div>
	</div>
</template>

<script>
	import headerTop from '@/components/public/headerTop'
	import leftMenu from '@/components/public/leftMenu'
	//import talkMain from '@/components/public/talk/talkMain'
	import titlePageTop from '@/components/public/titlePageTop'

	export default {
	  name: 'veiwMain',
	  components: {headerTop,leftMenu,titlePageTop},
	   data(){
	  	return{
	  		talkShowVal:false,
	  		menuChangeShow:true,
	  	}
	  },//data()
	  methods:{
	  	lisnToTack_on(){
	  		this.talkShowVal = !this.talkShowVal;
	  	},//lisnToTack_on()
	  	callTheLeftMenu(){
	  		//alert(1)
	  		this.menuChangeShow = !this.menuChangeShow;
	  	}//callTheLeftMenu
	  },//methods
	}
</script>


