<template>
	
	<div class="friendsPage">
		<el-row class="friends_top_seach">
			<el-col :span="16">  
				 <el-select v-model="value" placeholder="请选择">
				    <el-option
				      v-for="item in options"
				      :key="item.value"
				      :label="item.label"
				      :value="item.value">
				    </el-option>
				  </el-select>
			</el-col>
			<el-col :span="8" >
				 <el-input placeholder="请输入内容">
				    <el-button slot="append" icon="search"></el-button>
				  </el-input>
			</el-col>	
		</el-row>

		<el-row>
			<el-col :span="6" class="friends_list" v-for="n in 17">
				<div class="friends_list_content">

					<div class="friends_head_img">
						<img src="../../../assets/images/defulImage.png">
					</div>

					<p class="friends_nameText">好友的名字</p>

					<div class="friends_attribute">
						friends_attribute
					</div>

					<div class="friends_bottom_button">
						<el-button>发私信</el-button>
						<el-button>去主页</el-button>
					</div>

				</div>
			</el-col>
		</el-row>
	</div>

</template>

<script>
  export default {
    data() {
      return {
        options: [{
          value: '占位数据',
          label: '占位数据'
        }],
        value: ''
      }
    }
  }
</script>


