<template>
	<div class="mailListPage">
		<el-row class="theTable">
			<el-col :span="24" class="tableTitle">
				<div class="tableTitleText">
					<i class="icon-equalizer"></i>	控制面板
				</div>
			</el-col>
           	<el-col :span="24" class="tableContent">
           	<el-row>
           		<el-col :span="6" class="col-padding">
           				<el-input
						  placeholder="请入查询条件"
						  icon="search">
						</el-input>
           		</el-col>
           		<el-col :span="6"  class="col-padding">
           			<el-button><i class="icon-magnifier"></i> 查询</el-button>
           		</el-col>
           	</el-row>
		</el-col>
	   </el-row>

	   <el-row class="theTable">
			 <el-col :span="24" class="tableTitle">
			 	<div class="tableTitleText">
			 		<i class="icon-layers"></i>	通讯录
			 	</div>
			 </el-col>
	         <el-col :span="24" class="tableContent">

           		<el-table
			    :data="tableData"
			    border
			    style="width: 100%">
			     <el-table-column
			      prop="name"
			      label="姓名">
			    </el-table-column>
			    <el-table-column
			      prop="date"
			      label="警号">
			    </el-table-column>
			   	<el-table-column
			      prop="date"
			      label="电话">
			    </el-table-column>
			    <el-table-column
			      prop="address"
			      label="单位">
			    </el-table-column>
			  </el-table>


			   <el-pagination
			     :page-sizes="[100, 200, 300, 400]"
			     :page-size="100"
			     layout="total, sizes, prev, pager, next, jumper"
			     :total="400">
			   </el-pagination>

          	</el-col>
      </el-row>
	</div>
</template>


<script>
  export default {
    data() {
      return {
        tableData: [{
          date: '2016-05-02',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }, {
          date: '2016-05-04',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1517 弄'
        }, {
          date: '2016-05-01',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1519 弄'
        }, {
          date: '2016-05-03',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1516 弄'
        }]
      }
    }
  }
</script>


