<template>
	<div class="SiteplanMian">
		 <transition name="el-zoom-in-center">
		<div class="theWorkMain" v-show="!floatShow">
			<el-row class="theTable">
			<el-col :span="24" class="tableTitle">
				<div class="tableTitleText">
					<i class="icon-equalizer"></i>	控制面板
				</div>
			</el-col>
           	<el-col :span="24" class="tableContent">
				<el-button @click="changTheFloat('add')" ><i class="icon-plus"></i> 新增</el-button>
			</el-col>
	    </el-row>

		<el-row class="theTable">
			<el-col :span="24" class="tableTitle">
				<div class="tableTitleText">
					<i class="icon-layers"></i>	点位数据
				</div>
			</el-col>
           	<el-col :span="24" class="tableContent">

           	
           									<el-table
										    :data="tableData3"
										    border
										    style="width: 100%">
										    <el-table-column
										      prop="name"
										      label="点位名称">
										    </el-table-column>
										    <el-table-column
										      prop="date"
										      label="立杆类型">
										    </el-table-column>
										    
										    <el-table-column
										      prop="address"
										      label="在建点位">
										    </el-table-column>

										    <el-table-column
										      prop="address"
										      label="待建点位">
										    </el-table-column>
										    <el-table-column
										      label="操作">
										      <template scope="scope">
										        <el-button @click="changTheFloat('look')" size="small">查看</el-button>
										      </template>
										    </el-table-column>
										  </el-table>

										   <el-pagination
									      :current-page="currentPage4"
									      :page-sizes="[100, 200, 300, 400]"
									      :page-size="100"
									      layout="total, sizes, prev, pager, next, jumper"
									      :total="400">
									    </el-pagination>




				</el-col>

			</el-row>
		</div>
		</transition>
		 <transition name="el-zoom-in-center">
			<floatMian v-show="floatShow" v-on:lisnTheFn="lisnTheFn_on" v-bind:floatType="floatType"></floatMian>
		</transition>
	</div>
</template>

<script>
import floatMian from '@/components/managementOfLocation/Siteplan/floatMian'

  export default {
  	components: {floatMian},
    data() {
      return {
      	floatType:'',
      	floatShow:false,
      	currentPage4: 4,
        tableData3: [{
          date: '2016-05-03',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }, {
          date: '2016-05-02',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }, {
          date: '2016-05-04',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }, {
          date: '2016-05-01',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }, {
          date: '2016-05-08',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }, {
          date: '2016-05-06',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }, {
          date: '2016-05-07',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }]
      }
    },//data()
    methods:{
    	changTheFloat(msg){
    		//alert(msg)
    		this.$message('收到参数：' +msg);
    		this.floatType = msg;
    		this.floatShow = !this.floatShow;
    	},//changTheFloat
    	lisnTheFn_on(msg){
    		if(msg){
    			//alert(msg);
    			this.$message('收到参数：' +msg);
    			this.floatShow = !this.floatShow;
    		}else{
    			this.floatShow = !this.floatShow;
    		}
    	},//lisnTheFn_on
    },//methods
  }
</script>


